@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Outfit', sans-serif;
}

body{
  background: #000;
}

h1, h4,p, a{
  color: #fff;
  text-decoration: none;
}

ul{
  list-style-type: none;
}

.btn{
  padding: 12px, 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background-color: rgb(248, 217, 15);
  color: #222;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;
}

.btn-light{
  background: transparent;
  color: #fff;
}

.btn:hover {
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  transition: 0.3s;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}

.header-bg{
    background-color: rgb(0, 0, 0, 0.85);
    transition: 0.5s;
}

.nav-menu {
    display: flex;
}

.nav-menu li{
    padding: 0 1rem;
}

.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 500;
}

.hamburger{
    display: none;
}

@media screen and (max-width: 1040px) {
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgb(0, 0, 0, 0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
    }

    .nav-menu.active{
        left: 0;
    }

    .nav-menu li{
        padding: 1rem 0;
    }
    
    .nav-menu li a{
        font-size: 2rem;
    }
    .hamburger{
        display: initial;
    }
}

.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}

.intro_img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left:0;
    background-color: #000;
    opacity: 0.7;
}

.hero{
    height: 100%;
    width: 100%;
}

.hero .content{
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;

}

.hero .content h1{
    font-size: 3rem;
    padding: 0.6rem 0 0.6rem;
}

.hero .content p{
    font-size: 1.2rem;
    font-weight: 400;
    text-transform: uppercase;
}


.content .btn{
    position: relative;
    top: 20px;
    margin: 1rem 0.2rem;
    padding: 12px;
}

@media screen and (max-width:640px){
    .hero .content h1{
        font-size: 2rem;
        padding: 0.6rem 0 1.5rem;
    }
    
    .hero .content p{
        font-size: 1.0rem;
        text-transform: uppercase;
    } 
}

.content h1 {
    border-right: 5px, solid;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    -webkit-animation: 
    typing 2s steps(15),
    cursor .4s step-end infinite alternate;
            animation: 
    typing 2s steps(15),
    cursor .4s step-end infinite alternate;

}
  
  /* The typing effect */
  @-webkit-keyframes cursor{
    50% { border-color: transparent}
  }
  @keyframes cursor{
    50% { border-color: transparent}
  }
  /* The typewriter cursor effect */
  @-webkit-keyframes typing{
    from { width: 0}
  }
  @keyframes typing{
    from { width: 0}
  }

.footer{
    width : 100%;
    padding: 2rem 0;
    background-color: rgba(19, 19, 19, 0.8);
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location{
    display: flex;
    margin-bottom: 0.8rem;
}

.location p{
    line-height: 30px;
}

.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.social{
    margin-top: 1rem;
}

h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}

p{
    font-size: 1.2rem;
}

@media screen and (max-width: 640px){
    .footer-container{
        grid-template-columns: 1fr;
    }
}
.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.project-card{
    background: #1a1919;
    padding: 1.2rem 1rem;
}

.project-card:hover{
    background: #515050;
}

.project-card img{
    width: 100%;
}

@media screen and (max-width: 740px){
    .project-container{
        max-width: 90%;
        margin: auto;

        grid-template-columns: 1fr;
    }
}

.project-title{
    color: #fff;
    padding: 1rem 0;
}

.pro-details p{
    padding-bottom: 1rem;
    font-size: 1.1rem;
    /* text-align: justify; */
}

.pro-btns{
    display: flex;
    justify-content: space-around;
    padding: 0.5rem 0;
}

.pro-btns .btn{
    padding: 0.5rem 1rem;
}
.hero-img{
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    height: 80vh;
    position: relative;
}

.hero-img::before{
    content: "";
    background: url(https://images.unsplash.com/photo-1550439062-609e1531270e?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1170&q=80);
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    /* opacity: 0.7; */
}
.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-img h1{
    font-size: 2.4rem;
}

.hero-img p{
    font-size: 1.4rem;
    text-align: center;
}

@media screen and (max-width:640px){

    .hero-img{
        height: 80vah;
    }
    .hero-img h1{
        font-size: 2rem;
        padding: 0.6rem 0 1.5rem;
    }
    
    .hero-img  p{
        font-size: 1.0rem;
        text-transform: uppercase;
    } 
}
label{
    color: #fff;
    margin-bottom: 0.5rem;
}

form{
    display: flex;
    flex-direction: column;
    padding: 4rem 1rem;
    margin: auto;
    max-width: 600px;
}

form input, form textarea{
    margin-bottom: 1rem;
    font-size: 1.2rem;
    padding: 10px, 18px;
    background-color: rgba(255, 255, 255, 0.1);
    border-color: rgba(255, 255, 255, 0.1);
    color: #f4f4f4;
}
.about{
    width: 100%;
    margin: 3rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
}

.about .left{
    text-align: center;
    margin: auto;
    padding: 0rem;
    max-width: 40rem;
}

.about .left p{
    margin: 1.2rem 0;
}

.about .right{
    max-width: 700px;
}

.right .img-container{
    display: grid;
    grid-template-columns: repeat(15, 1fr);
    position: relative;
    align-items: center;
    text-align: center;
}

.right .img{
    width: 20rem;
    border: 1px solid #333;
    margin-right: 3rem;
}

.right .top{
    grid-column: 1 / span 8;
    grid-row: 1;
    padding-top: 20%;
    z-index: 6;
}

.right .bottom{
    grid-column: 4 / -1;
    grid-row: 1;
    z-index: 5;
}

.btn{
    position: relative;
    top: 20px;
    margin: 1rem 0.2rem;
    padding: 12px;
}

@media screen and (max-width: 740px){
    .about{
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}

